        </div>
        <div class="fenlei">
          <img v-on:click='changeBtsStatu(index)' v-for="item,index in watchBtsGroup" :src="item.src" alt="">
        </div>
        <div class="my-gift" ref="mygift">
          <div class="item">
            <div class="left">
              <div
                :style="{ 'background-image': 'url(' + giftDeitl.img + ')','background-repeat':'no-repeat','background-size':'cover' }"></div>
            </div>
            <div class="right">
              <div class="dis">
                {{giftDeitl.name}},{{giftDeitl.des}}
              </div>
              <p>
