                </div>
                <div class="go" v-on:click="getHelp">
                  喊人寻宝
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="content" ref="content">
          <ul class="gift-box">
            <li v-for="item in giftList.slice(0,3)" class="item" v-on:click="getGift(item.id,item.nowStock)">
              <div class="left">
                <div
                  :style="{ 'background-image': 'url(' + item.picResUrl + ')','background-repeat':'no-repeat','background-size':'cover' }"></div>
              </div>
              <div class="right">
                <div class="dis">{{item.name}},{{item.description}}</div>
                <div class="count">
                  <span></span>
                  剩余：{{item.nowStock}}
                </div>
                <div class="right-bottom">
                  <div class="free">
                    <span>￥</span>0
                  </div>
                  <div class="price">
                    市场价：{{item.price}}￥
                  </div>
                  <div class="go" v-show="item.nowStock != 0">
                    抱走它!
                  </div>
                  <div class="go" v-show="item.nowStock == 0">
                    提醒埋宝
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="more-gift">
        <ul class="gift-box">
          <li v-for="item in giftList.slice(3)" class="item" v-on:click="getGift(item.id,item.nowStock)">
            <div class="left">
              <div
                :style="{ 'background-image': 'url(' + item.picResUrl + ')','background-repeat':'no-repeat','background-size':'cover' }"></div>
            </div>
            <div class="right">
              <div class="dis">{{item.name}},{{item.description}}</div>
              <div class="count">
                <span></span>
                剩余：{{item.nowStock}}
              </div>
              <div class="right-bottom">
                <div class="free">
                  <span>￥</span>0
                </div>
                <div class="price">
                  市场价：{{item.price}}￥
                </div>
                <div class="go" v-show="item.nowStock != 0">
                  抱走它!
                </div>
                <div class="go" v-show="item.nowStock == 0">
                  提醒埋宝
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="a-rules">
        <div class="more" v-on:click="showMore" ref="moreButton"></div>
        <div class="bottom" style="background: #efe0c5;width: 6.15rem;margin:0 auto;padding: .3rem">
          <div class="title">
            <div class="line"></div>
            <span>活动规则</span>
            <div class="line"></div>
          </div>
          <div class="rule-content">
            <p>1、每个用户只能同时组建一支寻宝小队，当前寻宝任务成功后才能组建下一支寻宝小队；</p>
            <p>2、每个用户寻宝次数不限，不同礼品所需战力值不同；</p>
            <p>3、同一用户在同一篇「热门文章」内只能助力<span style="color:red;"> 1 </span>次，首次关注参与活动的用户可增加<span style="color:red;"> 10 </span>战力值，<span style="color: red;">再次助理</span>增加<span
              style="color:red;"> 1 </span>战力值；</p>
            <P>4、实物发货的时间为<span style="color:red;"> 7-10个工作日 </span>；</P>
            <p>5、请仔细核对收货电话和地址，地址一旦提交则<span style="color:red;"> 不能修改 </span>；</p>
            <p>6、如发现作弊行为，所获礼品视为无效；</p>
            <p>7、客服邮箱:campusmaster@baidu.com；</p>
            <p>8、本活动最终解释权在法律法规范围内归百度校园所有。</p>
          </div>
        </div>
      </div>
      <div class="share" ref="share">
        <img :src="shareList[nowShareImg]" alt="">
        <div class="change-img">
          <h5>点击菜单<span style="color:red">「热门文章」</span>，邀请好友在文章内<span style="color:red;">留言回复四位暗号</span>，为你助力！</h5>
          <!--<div class="button" v-on:click="changeImg">换一张</div>-->
        </div>
      </div>
      <div class="history" ref="history" v-on:click="goGiftDetil"></div>
      <div class="full-box" v-show="isSure">
        <div class="sure">
          <p>是否领取该宝物</p>
          <div v-on:click="sure('y')">确定</div>
          <div v-on:click="sure('n')">取消</div>
        </div>
      </div>
      <Loading v-show="loading"/>
    </div>
    <div v-if="!this.is_weixin()">
      <p style="font-size:20px;text-align: center;margin-top:3rem;padding-bottom:.1rem">长按识别二维码</p>
