<script>
  import PaoMaDen from '@/components/cangbaotu/PaoMaDen'
  import Loading from '@/components/shetuan/Loading'
  import SignIn from '../../lib/SignIn'
  import qs from 'qs'
  import axios from 'axios';
  import bt0 from '../../assets/cangbaotu/zhoubian01.png'
  import bt1 from '../../assets/cangbaotu/zhoubian02.png'
  import bt2 from '../../assets/cangbaotu/dianzi01.png'
  import bt3 from '../../assets/cangbaotu/dianzi02.png'
  import bt4 from '../../assets/cangbaotu/shuji01.png'
  import bt5 from '../../assets/cangbaotu/shuji02.png'

  export default {
    name: "IndexPage",
    data() {
      return {
        text: '',
        giftList: [],
        moreGiftList: [],
        message: '',
        giftDeitl: {
          name: '',
          des: '',
          img: '',
          time: {
            d: '0',
            h: '00',
            m: '00',
            s: '00',
            endTime: '',
            status: '',
          },
          needScore: ''
        },
        nowGift: {},
        shareList: [],
        nowShareImg: 0,
        start: 0,
        limit: 10,
        isSure: false,
        giftId: '',
        giftStock: '',
        loading: false,
        hasLogin: false,
        bts: [],
        bt_img: [bt0, bt1, bt2, bt3, bt4, bt5],
        recommendTab: null
      }
    },
    created() {
      for (var i = 0; i < this.bt_img.length / 2; i++) {
        this.bts.push({state: false})
      }
    },
    mounted() {
      // document.oncontextmenu=function(e){
      //   e.preventDefault();
      // };
      this.forbidden_wx_share();
      if (this.is_weixin()) {
        this.initPage();
      } else {
        alert('请在微信内打开!');
      }
    },
    methods: {
      initPage(str) {
        let _this = this;
        let data = {
          start: this.start,
          limit: this.limit
        };
        this.loading = true;
        if (this.recommendTab !== null) {
          data.recommendTab = this.recommendTab;
        }
        axios.post(_this.proxy + 'goodsList.do', qs.stringify(data)).then(function (response) {
          if (response.status === 200) {
            _this.loading = false;
            if (response.data.error_no == '0') {
              let data = response.data.data.datas;
              if (str !== 'more') {
                _this.giftList = [];
              }
              for (var i = 0; i < data.length; i++) {
                // if(data[i].name != '小度智能音箱' && data[i].name != '小度在家智能视频音箱' && data[i].name != '小度智能音箱 Pro'){
                // }
                _this.giftList.push(data[i]);
              }
              _this.queryMyWinGoodsCount();
              _this.hasLogin = false;
              if (str == undefined) {
                _this.getMyself();
              }
            }
            if (response.data.error_no == '2') {
              _this.hasLogin = true;
            }
          }
        })
      },
      changeBtsStatu(index) {
        for (var i = 0; i < this.bts.length; i++) {
          this.bts[i].state = false;
        }
        this.bts[index].state = !this.bts[index].state;
        this.recommendTab = index;
        this.start = 0;
        this.initPage(index);
        /*
        0 0 1
        1 2 3
        2 4 5
         */
      },
      showMore() {
        this.start += 10;
        this.initPage('more');
      },
      getMyself() {
        let _this = this;
        axios.get(_this.proxy + 'queryWiningGoods.do ').then(function (res) {
          // 状态为0正在夺宝
          // 状态1已完成夺宝
          // 状态2为失败
          // 状态3为领取成功
          if (res.status === 200) {
            if (res.data.error_no == '0') {
              let records = res.data.data;
              _this.$refs.mygift.style.display = 'block'
              _this.giftDeitl.name = records.goodsEntity.name;
              _this.giftDeitl.des = records.goodsEntity.description;
              _this.giftDeitl.img = records.goodsEntity.picResUrl;
              if (records.supportScoresTotal) {
                _this.giftDeitl.needScore = records.scores - records.supportScoresTotal;
              } else {
                _this.giftDeitl.needScore = records.scores;
              }
              if (records.shareUrl !== undefined) { //分享图片，图片可能为undefined
                _this.shareList = JSON.parse(records.shareUrl);
              }
            }
          }
        })
      },
      queryMyWinGoodsCount() {
        axios.get(this.proxy + 'queryMyWinGoodsCount.do').then(res => {
          if (res.status === 200) {
            if (res.data.error_no == '0') {
              if (res.data.data != '0') {
                this.$refs.history.style.display = 'block';
              } else {
                this.$refs.history.style.display = 'none';
              }
            }
          }
        })
      },
      goGiftDetil() {
        this.$router.push({path: 'detail'})
      },
      getHelp() {
        this.$refs.share.style.display = 'block'
      },
      changeImg() {
        if (this.nowShareImg >= this.shareList.length - 1) {
          this.nowShareImg = 0;
        } else {
          this.nowShareImg++;
        }
      },
      getGift(id, stock) {
        let _this = this;
        this.giftId = id;
        this.giftStock = stock;
        if (stock == 0) {
          alert('校园君已经收到通知,会尽快埋宝')
        } else {
          _this.isSure = true;
        }
      },
      sure(str) {
        let _this = this;
        this.isSure = false;
        if (str == 'y') {
          this.loading = true;
          axios.get(_this.proxy + 'startFindGoods.do?goodsId=' + _this.giftId, {}).then(function (response) {
            if (response.status == 200 && response.data.error_no == '0') {
              _this.loading = false;
              _this.$router.push({path: 'detail'})
            }
            if (response.data.error_no == '1' && response.status == 200) {
              _this.loading = false;
              alert(response.data.data.message)
              _this.$router.push({path: 'detail'})
            }
          })
        }
      },
      login() {
        window.location.href = location.href + '?time=' + ((new Date()).getTime());
        location.reload();
      }
    },
    computed: {
      watchBtsGroup: function () {
        for (var i = 0; i < this.bts.length; i++) {
          this.bts[i].src = this.bts[i].state == true ? this.bt_img[2 * i] : this.bt_img[2 * i + 1]
        }
        return this.bts;
      }
    },
    components: {
      PaoMaDen,
      Loading,
      SignIn
    }
  }
</script>

