<template>
  <div class="loading">
    <img src="../../assets/loading.gif" alt="">
  </div>
</template>

<script>
  export default {
    name: 'Loading',
    data () {
      return {}
    },
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus">
  .loading{
    position: fixed;
    top:0;
    left:0;
    bottom 0
    right 0
    z-index:9999;
    background: rgba(0,0,0,0.3);
  }
  .loading {
    img {
      width 1rem
      height 1rem
      position absolute
      top 0
      bottom 0
      right 0
      left 0
      margin auto
    }
  }
</style>



