<template>
  <div class="wrap">
    <div class="box" ref="box">
      <!--<div v-for="i in data" class="marquee" ref="item">-->
      <!--<span>{{i.join}}</span>-->
      <!--<span>加入</span>-->
      <!--<span>{{i.Organizer}}</span>-->
      <!--<span>的寻宝小队，帮TA获得了</span>-->
      <!--<span style="color: #f2d85b;">{{i.gift}}</span>-->
      <!--</div>-->
      <div v-for="item in datas" class="marquee" ref="item">
        <span style="color: #f2d85b;">{{item.name}}</span>
        <span>参与了本活动</span>
        <!--<span style="color: #f2d85b;">{{item.gift}}</span>-->
      </div>
      <div id="node" ref="node"></div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'

  export default {
    name: 'PaoMaDen',
    data() {
      return {
        data: [
          {
            Organizer: '巧克力',
            join: '花生',
            gift: '爱奇艺月卡'
          },
          {
            Organizer: '这个人',
            join: '那个人',
            gift: '一些好吃的'
          },
          {
            Organizer: '金轮法王',
            join: '乔峰',
            gift: '倚天屠龙剑'
          },
          {
            Organizer: '孙悟空',
            join: '唐僧',
            gift: '苹果笔记本'
          }
        ],
        datas: [],
        animate: true
      }
    },
    methods: {
      move() {
        let width = document.getElementById('node').offsetWidth;
        let box = this.$refs.box;
        let distance = 20
        let fn = setInterval(() => {
          distance = distance - 1
          if (-distance >= width) {
            distance = 20;
          }
          box.style.transform = 'translateX(' + distance + 'px)'
        }, 30)
      }
    },
    mounted() {
      let _this = this;
      let nodeDom = this.$refs.node;
      axios.get(this.proxy + 'getNewsHistory.do', {}).then(function (res) {
        if (res.data.error_no == '0') {
          for (var i = 0; i < res.data.data.length; i++) {
            _this.datas.push({name: res.data.data[i].userEntity.nickname, gift: res.data.data[i].goodsEntity.name});
            nodeDom.innerHTML += `<span>${res.data.data[i].userEntity.nickname}</span>参与了本活动`;
          }
          _this.move();
        }
      })
    }
  }
</script>
<style scoped lang="stylus">
  .wrap {
    overflow: hidden;
    width 6.53rem;
    height .44rem;
    margin 0 auto
    background rgba(0, 0, 0, 0.4);
    border-radius 0.4rem;
    line-height .44rem;
    position relative
  }

  .box {
    width 100rem;
    height: 100%;
    overflow: hidden;
    position absolute
    top 0
    left 0
  }

  .box div {
    float: left;
  }

  .marquee {
    margin: 0 0.4rem 0 0;
    white-space: nowrap;
    font-size 0.2rem;
    color #fff;
  }

  #node {
    font-size 0.2rem;
    white-space nowrap
    position: absolute
    z-index: -999;
    visibility hidden
  }
</style>



